
.card{
    height: 30%;
    width: auto;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex: auto;
    width: 70vw;
    max-width: 750px;
    border: 2px solid #60564a;
    margin: 10px;
    color: #3f3942;
    font-family: 'Amatic SC', cursive;
    font-weight: bold;
    font-size: 50px;
    padding: 10px;
    background-color: rgb(165, 176, 164, 0.2);
  }

  .active-bio-card{
    height: 30%;
    width: auto;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex: auto;
    width: 70vw;
    max-width: 750px;
    border: 2px solid #60564a;
    /* margin: 10px; */
    color: #3f3942;
    font-family: 'Amatic SC', cursive;
    font-weight: bold;
    /* font-size: 50px; */
    padding: 10px;
    background-color: rgb(165, 176, 164, 0.2);
  }

  .bio-splash-img{
    margin: 10px;
    margin-right: 30%;
    height: 30%;
    width: 30%;
    text-align: left;
    border-radius: 90px;
    border: 1px solid #60564a;
   }

 
